@import '../../../styles/settings.scss';

.root {
  display: flex;
  justify-content: space-between;

  .navigation {
    margin: 0;
    padding: 0;
    display: flex;
    list-style: none;

    &Item {
      margin-right: 32px;
    }

    &Link {
      font-size: 14px;
      color: #aaa;
      text-decoration: none;
      font-weight: 600;
      text-transform: uppercase;
      transition: .2s;

      &:hover, &.active {
        color: #fff;
      }
    }
  }
}
