@import '../../../styles/settings.scss';

.root {
  padding: 60px 0;

  .linkWrapper {
    text-align: right;
  }

  .link {
    text-decoration: none;
    padding: 6px 12px;
    color: #fff;
    background-color: green;
    border-radius: 10px;
    transition: .2s;

    &:hover {
      background-color: rgb(7, 167, 7);
    }
  }

  @media (max-width: 800px) {
    padding: 20px 0;
  }
}
