.root {
  margin: 0 auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.shortData {
  text-align: left;
}

.content {
  text-align: justify;
}

.description {
  display: flex;
  overflow: hidden;

  .textContent {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .image {
    width: 450px;
    max-width: 100%;
    margin-right: 20px;
  }

  .price {
    margin-bottom: 0;
    text-align: right;
  }

  .priceItem {
    display: inline-block;
    position: relative;
    background-color: orangered;
    color: #fff;
    padding: 5px 5px 5px 30px;
    border-radius: 10px;
  }
}

@media (max-width: 800px) {
  .root {
    max-width: 500px;
  }

  .shortData {
    text-align: right;
  }

  .header {
    flex-direction: column;
    align-items: flex-end;
  }

  .description {
    flex-direction: column;

    .image {
      width: 100%;
      align-self: flex-end;
      margin-right: 0;
    }
  }
}
