@import '../../../styles/settings.scss';

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  .link {
    text-decoration: none;
    padding: 5px 10px;
    background-color: #258525;
    color: #fff;
    border-radius: 5px;
    transition: .2s;

    &:hover {
      background-color: #38aa38;
    }
  }
}

.paginationWrapper {
  min-width: 320px;
  width: 100%;
  position: fixed;
  bottom: 0;
  padding: 6px 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background-color: #eee;
  box-shadow: 0 -4px 4px #aaa;
  color: #fff;
}
