@import '../../../styles/settings.scss';


.postList {
  list-style: none;
  margin: 0;
  padding: 0;
  perspective: 800;

  .postItem {
    margin: 20px 0;

    .postTitleLink {
      display: block;
      font-size: 16px;
      box-shadow: 2px 2px 4px #555;
      border-radius: 5px;
      padding: 10px 10px;
      text-decoration: none;
      color: #888;
      transition: .3s;

      &:hover {
        transform: rotateY(1deg) rotateX(-1deg);
        background-color: #258525;
        color: #fff;
      }
    }
  }
}
