.root {
  max-width: 600px;
  padding-bottom: 30px;
  margin: 0 auto;


  .inputBig {
    width: 100%;
  }

  .formControl {
    margin: 20px 0;
  }

  .smallControls {
    display: flex;
    justify-content: space-between;
  }

  .status {
    width: 130px;
  }

  .btnControl {
    text-align: center;
  }

  .price {
    width: 130px;
  }

  .content {
    width: 100%;
    min-height: 250px;
    font-size: 18px;
    padding: 10px;
  }

  .error {
    border-color: red;
  }
}

